"use client";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Separator } from "@/components/ui/separator";
import { ArrowLeft, Package, Truck, CheckCircle } from "lucide-react";
import Link from "next/link";
import postService from "@/services/postService";

const Checkout = () => {
  const [checkout, setCheckout] = useState([]);
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const router = useRouter();

  useEffect(() => {
    const fetchCheckout = async () => {
      try {
        const response = await postService.getCheckout();
        setCheckout(response.data.checkout || []);
      } catch (error) {
        console.error("Error fetching checkout:", error);
        setError("No Checkout items found.");
      } finally {
        setLoading(false);
      }
    };
    fetchCheckout();
  }, []);

  useEffect(() => {
    fetch("http://localhost:8001/api/checkAuth", {
      method: "GET",
      credentials: "include",
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.isAuthenticated) {
          setUser({
            id: data.userId,
            username: data.username,
            email: data.email,
            profile: data.profile ? data.profile : null,
          });
          setLoading(false);
        }
      });
  }, [router]);

  if (loading) {
    return (
      <div className="container mx-auto px-4 py-12 text-center">
        <p>Loading your orders...</p>
      </div>
    );
  }

  if (error) {
    return (
      <div className="container mx-auto px-4 py-12 text-center">
        <p className="text-red-500">{error}</p>
        <Button className="mt-4" onClick={() => router.push("/")}>
          Return to Home
        </Button>
      </div>
    );
  }

  // Calculate order totals
  const calculateOrderTotal = (items) => {
    const subtotal = items.reduce((total, item) => {
      return total + item.service_id.amount * item.quantity;
    }, 0);
    const shipping = 12.99;
    return { subtotal, shipping, total: subtotal + shipping };
  };

  const { subtotal, shipping, total } = calculateOrderTotal(checkout);

  return (
    <main className="container mx-auto px-4 py-12">
      <div className="flex items-center mb-8">
        <Link
          href="/"
          className="flex items-center text-sm font-medium text-muted-foreground hover:text-foreground"
        >
          <ArrowLeft className="mr-2 h-4 w-4" />
          Back to Shopping
        </Link>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        <div className="lg:col-span-2">
          <div className="bg-white rounded-lg shadow-sm p-6">
            <div className="space-y-6">
              <div className="flex items-center justify-between text-sm font-medium">
                <h3 className="text-lg">Order Items</h3>
                <span>{checkout.length} items</span>
              </div>

              <Separator />

              {checkout.map((item, index) => (
                <div
                  key={index}
                  className="grid grid-cols-1 md:grid-cols-12 gap-4 py-4"
                >
                  <div className="md:col-span-2">
                    <div className="h-20 w-20 bg-gray-100 rounded">
                      <img
                        src={`http://localhost:8001/items/${item.service_id.photo}`}
                        alt={item.service_id.name}
                        className="h-full w-full object-cover rounded"
                      />
                    </div>
                  </div>
                  <div className="md:col-span-4">
                    <h4 className="font-medium">{item.service_id.name}</h4>
                    {item.service_id.subtitle && (
                      <p className="text-sm text-muted-foreground">
                        {item.service_id.subtitle}
                      </p>
                    )}
                    {(item.service_id.color || item.service_id.size) && (
                      <p className="text-sm text-muted-foreground mt-1">
                        {item.service_id.color &&
                          `Color: ${item.service_id.color}`}
                        {item.service_id.color && item.service_id.size && " | "}
                        {item.service_id.size &&
                          `Size: ${item.service_id.size}`}
                      </p>
                    )}
                  </div>
                  <div className="md:col-span-2 text-center">
                    <p className="text-sm text-muted-foreground">Status</p>
                    <span
                      className={` ${
                        item.status === "processing"
                          ? "bg-yellow-100 text-yellow-800"
                          : item.status === "completed"
                          ? "bg-blue-100 text-blue-800"
                          : "bg-red-100 text-red-800"
                      } px-3 py-1 rounded-full text-sm`}
                    >
                      {item.status}
                    </span>
                  </div>
                  <div className="md:col-span-2 text-center">
                    <p className="text-sm text-muted-foreground">Quantity</p>
                    <p>{item.quantity}</p>
                  </div>
                  <div className="md:col-span-2 text-right">
                    <p className="text-sm text-muted-foreground">Price</p>
                    <p className="font-medium">
                      $
                      {(item.service_id.amount * item.quantity + 150).toFixed(
                        2
                      )}
                    </p>
                    <p className="font-light text-[0.6rem]">
                      Including VAT + Delivery
                    </p>
                  </div>
                  <Separator className="md:col-span-12 mt-2" />
                </div>
              ))}
            </div>
          </div>
        </div>

        <div className="lg:col-span-1">
          <div className="bg-white rounded-lg shadow-sm p-6 sticky top-24">
            <h2 className="text-lg font-medium mb-4">Order Summary</h2>

            <div className="space-y-3 text-sm">
              <div className="flex justify-between">
                <span className="text-muted-foreground">Subtotal</span>
                <span>${subtotal.toFixed(2)}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Shipping</span>
                <span>${shipping.toFixed(2)}</span>
              </div>
              <Separator className="my-3" />
              <div className="flex justify-between font-medium text-base">
                <span>Total</span>
                <span>${total.toFixed(2)}</span>
              </div>
            </div>

            <div className="mt-8 space-y-4">
              <div className="flex items-center text-sm">
                <Package className="h-4 w-4 mr-2 text-muted-foreground" />
                <span>Your order is being prepared</span>
              </div>
              <div className="flex items-center text-sm">
                <Truck className="h-4 w-4 mr-2 text-muted-foreground" />
                <span>Estimated delivery: 3-5 business days</span>
              </div>
              <div className="flex items-center text-sm">
                <CheckCircle className="h-4 w-4 mr-2 text-green-500" />
                <span>Payment confirmed</span>
              </div>
            </div>

            <Button className="w-full mt-6 bg-black hover:bg-gray-800 text-white">
              Track Order
            </Button>
          </div>
        </div>
      </div>
    </main>
  );
};

export default Checkout;
